import { Component, Inject, OnInit } from '@angular/core';
import { MOVIES } from './mock-movies';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material';
import { CATEGORIES } from '../movie-categories/mock-categories';

@Component({
  selector: 'app-movies',
  templateUrl: './movies.component.html',
  styleUrls: ['./movies.component.css']
})
export class MoviesComponent implements OnInit {
  movies = MOVIES;
  displayedColumns = ['id', 'title', 'actions'];

  openEditor(movie) {
    this.dialog.open(MovieEditorComponent, {
      data: movie,
      height: '400px',
      width: '600px',
    });
  }

  onNew() {
    const movie = {
      'id': '',
      'title': '',
      'categories': [],
      'thumbnails': {
        '400x207': '',
        '293x293': '',
        '295x144': '',
        '640x333': '',
        '341x307': ''
      },
      'synopsis': '',
      'abridged_cast': [{
        'name': ''
      }],
      'links': {
        'download': ''
      }
    };
    this.openEditor(movie);
  }

  onEdit(movie) {
    this.openEditor(movie);
  }

  constructor(private dialog: MatDialog) {
  }

  ngOnInit() {
  }
}

@Component({
  selector: 'app-movie-editor',
  templateUrl: './movie-editor.component.html',
  styleUrls: ['./movie-editor.component.css']
})
export class MovieEditorComponent implements OnInit {
  categories = CATEGORIES;

  constructor(@Inject(MAT_DIALOG_DATA) private movie: any) {
  }

  onSubmit(movie) {
  }

  addCast() {
    this.movie.abridged_cast.push({name: ''});
  }

  ngOnInit() {
  }
}


