import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MaterialModule } from './material.module';
import { AppComponent } from './app.component';
import { MoviesComponent, MovieEditorComponent } from './movies/movies.component';
import { MovieCategoriesComponent } from './movie-categories/movie-categories.component';
import { SongsComponent } from './songs/songs.component';
import { SongCategoriesComponent } from './song-categories/song-categories.component';
import { FormsModule } from '@angular/forms';

@NgModule({
  declarations: [
    AppComponent,
    MoviesComponent,
    MovieCategoriesComponent,
    SongsComponent,
    SongCategoriesComponent,
    MovieEditorComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    MaterialModule
  ],
  entryComponents: [
    MovieEditorComponent
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule {
}



